.wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;

    .campbar {
        width: 100%;
        background-color: var(--campbar-bg);
        padding: 20px 15px;
        text-align: center;

        .content {
            color: var(--white);
            font-size: 1.2rem;
            font-weight: 400;
            text-transform: uppercase;
        }

        .close {
            color: var(--white);
            background-color: transparent;
            border: none;
            float: right;
        }
    }

    .topbar {
        color: var(--black);
        border-bottom: 1px solid hsla(0, 0%, 100%, 0.15);

        .item {
            display: flex;
            align-items: center;

            .split {
                width: 1px;
                background-color: #ccc;
                height: 18px;
                display: inline-block;
                margin: 0px 15px;
            }
            a {
                text-decoration: none;
                font-size: 1.2rem;
                line-height: 45px;
                font-weight: 400;
                color: var(--white);

                &:hover {
                    color: var(--hover-color);
                }
            }

            .icon {
                font-size: 1.5rem;
                color: var(--white);

                & + a {
                    margin-left: 10px;
                }
            }
        }
    }

    .navbar {
        .logo {
            display: inline-block;

            img {
                width: auto;
                height: 80px;
                object-fit: cover;
            }
        }

        .menu {
            background-color: transparent;
            margin-left: 65px;
            flex: 1;

            .menu-link {
                font-size: 12px;
                text-transform: uppercase;
                letter-spacing: 1px;
                font-weight: 500;
                background-color: transparent;
                color: var(--white) !important;

                // &::before {
                //     content: '';
                //     height: 2px;
                //     transform: scale(0);
                //     width: calc(100% - 20px);
                //     background-color: var(--white);
                //     position: absolute;
                //     bottom: 2px;
                // }

                // &.active {
                //     &::before {
                //         transform: scale(1);
                //     }
                // }
            }

            ul {
                background-color: var(--transparent);
                border-bottom: 0;

                // li {
                //     @keyframes menuLinkHover {
                //         to {
                //             transform: scale(1);
                //         }
                //     }

                //     &:hover .menu-link::before {
                //         display: block;
                //         animation: menuLinkHover 0.3s ease-in-out forwards;
                //     }
                // }
            }
        }

        .actions {
            div {
                cursor: pointer;

                &:hover .icon {
                    transform: translateY(-2px);
                    transition: all 0.3s ease;
                }
            }

            .icon {
                font-size: 2rem;
                color: var(--white);
            }

            .p-15-hor {
                padding: 0px 15px;
            }

            .cart {
                .cart-size {
                    display: block;
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    text-align: center;
                    background: #fff;
                    line-height: 15px;
                    top: -7px;
                    right: 0;
                }
            }
        }
    }

    &.AboutUs,
    &.Product {
        position: unset;

        .topbar {
            background-color: #242424;
            border: 0;
        }
        .navbar {
            position: absolute;
            width: 100%;
        }
    }

    &.Product {
        .navbar {
            border-bottom: 1px solid hsla(0, 0%, 100%, 0.15);
        }
    }

    &.Contact,
    &.ProductDetail,
    &.Billing,
    &.Account,
    &.WishList {
        position: unset;

        .topbar {
            background-color: #242424;
            border: 0;
        }

        .navbar {
            border-bottom: 1px solid #e5e5e5;

            .menu {
                li {
                    .menu-link {
                        color: var(--black) !important;
                    }

                    .icon {
                        color: var(--black) !important;
                    }
                }
            }
            .actions {
                .actions-item {
                    color: var(--black);

                    &:hover {
                        color: var(--hover-color);
                    }

                    a,
                    .icon {
                        color: currentColor;
                    }
                    .cart-size {
                        background-color: var(--black);
                        color: var(--white);
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 992px) {
    .header-responsive.wrapper {
        background: var(--black);

        .topbar {
            display: none !important;
        }

        .navbar {
            position: unset;
            color: var(--white);

            .sidebar {
                padding: 15px;
                font-size: 2rem;
                color: currentColor;

                .icon {
                    cursor: pointer;
                }
            }

            .logo {
                width: 100%;
                text-align: center;
            }

            .menu {
                position: fixed;
                left: -100%;
                transition: all 0.7s linear;
                margin: 0;
                top: 0;
                bottom: 0;
                background-color: var(--white);
                width: 250px;
                max-width: 250px;
                z-index: 3;

                &.show {
                    left: 0;
                }

                .menu-link {
                    color: var(--black) !important;
                }

                // ul {
                //     padding: 20px;
                //     flex-direction: column;

                //     li {
                //         padding: 15px 0px;

                //         .menu-link {
                //             color: var(--black) !important;
                //         }
                //     }
                // }
            }
        }

        &.Product,
        &.WishList,
        &.ProductDetail,
        &.Contact,
        &.Billing,
        &.Account {
            background-color: var(--white);

            .navbar {
                color: var(--black);

                .actions {
                    .icon {
                        color: var(--black);
                    }

                    .cart-size {
                        background-color: var(--hover-color);
                        color: var(--white);
                    }
                }
            }
        }
    }
}
